
import { Constants } from "../Data/constants";
import AudioManager from "../Plugin/AudioManager";
import { GameController } from "./GameController";
import NodeContainer from "./NodeContanier";

const { ccclass, property } = cc._decorator;

@ccclass
export default class GamePlay extends cc.Component {
  @property(AudioManager)
  AudioManager: AudioManager = null;
  @property(GameController)
  GameController: GameController = null;
  @property(NodeContainer)
  NodeContainer: NodeContainer = null;


  // state
  anims: cc.Tween[] = [];


  protected onLoad(): void {

  }


  protected start(): void {
    // this.AudioManager.playSound(Constants.SoundTrack.bgSound);
    this.activeBtnAnim();
  }


  private activeBtnAnim(): void {
    this.anims[0] = cc.tween(this.NodeContainer.btn_Active)
      .repeatForever(
        cc.tween(this.node)
          .to(0.5, { scale: 0.9 })
          .to(0.5, { scale: 1 }, { easing: 'elasticOut' })
      ).start();
  }


  public handleStep1(): void {
    Constants.isDoneStep1 = true;
    let kimtiem = this.NodeContainer.Step1_btn_KimTiem.getChildByName("kimtiem");
    let frame = this.NodeContainer.Step1_btn_KimTiem.getChildByName("frame");
    let char_bone = this.NodeContainer.Step1_character.getChildByName("bone");
    let char_main = this.NodeContainer.Step1_character.getChildByName("main");

    this.anims[0].stop();
    this.NodeContainer.btn_Active.scale = 1;

    // tween kim tiem
    cc.tween(kimtiem)
      .to(0.5, { scale: 2 }, { easing: 'elasticOut' })
      .call(() => {

        cc.tween(kimtiem)
          .to(0.5, { x: 70 })
          .call(() => {

            // scale down frame
            cc.tween(frame)
              .to(1.5, { scaleY: 0 })
              .call(() => {

                // show bone character
                cc.tween(char_bone)
                  .to(0.5, { opacity: 255 })
                  .call(() => {
                    cc.tween(kimtiem)
                      .to(0.5, { opacity: 0 })
                      .start();
                  })
                  .start();
                // --------------

                // hide main character
                cc.tween(char_main)
                  .to(0.5, { opacity: 0 })
                  .start();
                // --------------
              })
              .start();
          })
          .start();

      })
      .start();

  }


}
